#tour {
	padding: 50px;
  flex-direction: row;
	align-content: left;
}

	.dates{
		display: flex;
	  flex-direction: row;
	  flex-wrap: wrap;
	  border: 1px solid #000;
	  margin: 1em 0;
	  justify-content: space-between;

	  ul{
	  	width: 100%;
	    
			li {
		  	list-style: none;
		  	width: 33%;
	   		display: inline-block;
	   		padding:20px;

	   		a{
	   			display: inline-block;
					background: grey;
					padding: 5px 10px;
					color: white; 
					font-size: 1.1rem;
					border-color: white;
					border:auto;
	   		}
	   	}	
		}
  }
 
